import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faQuoteLeft } from "@fortawesome/free-solid-svg-icons";
import { faQuoteRight } from "@fortawesome/free-solid-svg-icons";
import { faTwitter } from '@fortawesome/free-brands-svg-icons';
import './App.css';

const quotesDatabase = [
  {
    content: "That's your best friend and your worst enemy - your own brain.",
    author: "Fred Durst",
    color1: "#C4E0CF",
    color2: "#D5F1E0"
  },
  {
    content: "Whether it's the best of times or the worst of times, it's the only time we've got.",
    author: "Art Buchwald",
    color1: "#747FE6",
    color2: "#8590F7"
  },
  {
    content: "The best revenge is massive success.",
    author: "Frank Sinatra",
    color1: "#A8A6DB",
    color2: "#B9B7EC"
  },
  {
    content: "There are some things you learn best in calm, and some in storm.",
    author: "Willa Cather",
    color1: "#A6D2E1",
    color2: "#B7E3F2"
  },
  {
    content: "Living well is the best revenge.",
    author: "George Herbert",
    color1: "#33AA33",
    color2: "#44BB44"
  },
  {
    content: "To give anything less than your best is to sacrifice the gift.",
    author: "Steve Prefontaine",
    color1: "#EFCFC3",
    color2: "#FFDFD3"
  },
  {
    content: "A problem is a chance for you to do your best.",
    author: "Duke Ellington",
    color1: "#D3BAE9",
    color2: "#E4CBF0"
  },
  {
    content: "The best way to predict the future is to create it.",
    author: "Peter Drucker",
    color1: "#0000E8",
    color2: "#0000F9"
  },
  {
    content: "The way to get started is to quit talking and begin doing.",
    author: "Walt Disney",
    color1: "#EE0007",
    color2: "#FF0018"
  }
]

let DB = true;
let randomNumber = Math.floor(Math.random() * 9);

class QuoteBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quote: quotesDatabase[randomNumber].content,
      author: quotesDatabase[randomNumber].author,
      hoverColor: quotesDatabase[randomNumber].color2,
      hoverColorTweet: quotesDatabase[randomNumber].color2,
      mainColor: quotesDatabase[randomNumber].color2
    }
    this.handleTransition = this.handleTransition.bind(this);
    this.handleColor = this.handleColor.bind(this);
    this.handleColorOut = this.handleColorOut.bind(this);
  }
  handleTransition(event) {
    let oldRandom = randomNumber;
    while (oldRandom === randomNumber) {
      randomNumber = Math.floor(Math.random() * 9);
    }
    document.getElementById("quote1").style.opacity = 0;
    document.getElementById("text").style.opacity = 0;
    document.getElementById("quote2").style.opacity = 0;
    document.getElementById("tweet-button").style.opacity = 0;
    document.getElementById("new-quote").style.opacity = 0;
    document.getElementById("author").style.opacity = 0;
    DB = false;
    setTimeout(function() {
      this.setState({
        quote: quotesDatabase[randomNumber].content,
        author: quotesDatabase[randomNumber].author,  
        hoverColor: quotesDatabase[randomNumber].color2,
        hoverColorTweet: quotesDatabase[randomNumber].color2,
        mainColor: quotesDatabase[randomNumber].color2
      })
      document.getElementById("quote1").style.opacity = 1;
      document.getElementById("text").style.opacity = 1;
      document.getElementById("quote2").style.opacity = 1;
      document.getElementById("tweet-button").style.opacity = 1;
      document.getElementById("new-quote").style.opacity = 1;
      document.getElementById("author").style.opacity = 1;
      DB = true;
    }.bind(this), 1000)
  }
  handleColor(event) {
    if (DB == true) {
      if (event.target.id == "new-quote") {
        this.setState({
          hoverColor: quotesDatabase[randomNumber].color1
        })
      }
      else {
        this.setState({
          hoverColorTweet: quotesDatabase[randomNumber].color1
        })
    }
    }
  }
  handleColorOut(event) {
    if (DB == true) {
      this.setState({
        hoverColor: quotesDatabase[randomNumber].color2,
        hoverColorTweet: quotesDatabase[randomNumber].color2
      })
    }
  }
  render() {
    document.getElementsByTagName("body")[0].style.background = this.state.mainColor;
    return (
      <div id="quote-box" style={{color: this.state.mainColor}}>
        <FontAwesomeIcon icon={faQuoteLeft} id="quote1"></FontAwesomeIcon>
        <p id="text">{this.state.quote}</p>
        <FontAwesomeIcon icon={faQuoteRight} id="quote2"></FontAwesomeIcon>
        <p id="author"> -{this.state.author}</p>
        <a href="twitter.com/intent/tweet" target="_blank" id="tweet-quote"><button id="tweet-button" style={{backgroundColor: this.state.hoverColorTweet}} onMouseOver={this.handleColor} onMouseOut={this.handleColorOut}> <FontAwesomeIcon icon={faTwitter}></FontAwesomeIcon> </button></a>
        <button id="new-quote" style={{backgroundColor: this.state.hoverColor}} onMouseOver={this.handleColor} onMouseOut={this.handleColorOut}onClick={this.handleTransition}> New Quote </button>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <QuoteBox/>
    );
  }
}

export default App;